import Vue from "vue";
import VueRouter from "vue-router";

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location, onResolve, onReject) {
  if (onResolve || onReject){
    return originalPush.call(this, location, onResolve, onReject)
  }
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: {
      name: "index"
    }
  },
  {
    path: "/index",
    name: "index",
    component: () => import("views/Index/Index"),
    children: [
      {
        path: "home",
        name: "home",
        component: () => import("views/Index/Home"),
        meta: {
          title: ""
        }
      },
      {
        path: "category",
        name: "category",
        component: () => import("views/Index/Category"),
        meta: {
          title: ""
        }
      },
      {
        path: "cart",
        name: "cart",
        component: () => import("views/Index/Cart"),
        meta: {
          title: "",
          requireAuth: true
        }
      },
      {
        path: "personal",
        name: "personal",
        component: () => import("views/Index/Personal"),
        meta: {
          title: ""
        }
      }
    ],
    redirect: {
      name: "home"
    }
  },
  {
    path: "/order",
    name: "order",
    component: () => import("views/Order"),
    meta: {
      title: "",
      requireAuth: true
    }
  },
  {
    path: "/search",
    name: "search",
    component: () => import("views/Search"),
    meta: {
      title: ""
    }
  },
  {
    path: "/searchResult/:keyword",
    name: "searchResult",
    component: () => import("views/SearchResult"),
    meta: {
      title: ""
    }
  },
  {
    path: "/login",
    name: "login",
    component: () => import("views/Login"),
    meta: {
      title: ""
    }
  },
  {
    path: "/registration",
    name: "registration",
    component: () => import("views/Registration"),
    meta: {
      title: ""
    }
  },
  {
    path: "/product/:pid",
    name: "product",
    component: () => import("views/Product"),
    meta: {
      title: "",
      requireAuth: true
    }
  },
  {
    path: "/404",
    name: "404",
    component: () => import("views/Common/404"),
    meta: {
      title: ""
    }
  },
  {
    path: "*",
    redirect: {
      name: "404"
    }
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

router.push = function push(location, onResolve, onReject) {
  if (onResolve || onReject) {
    return originalPush.call(this, location, onResolve, onReject);
  }
  return originalPush.call(this, location).catch((err) => {
    let reg = new RegExp("^Redirected when going from \"[a-z_.\\/]+\" to \"[a-z_.\\/]+\" via a navigation guard.$");
    if (reg.test(err.message)) {
      return Promise.resolve(false);
    }
    return Promise.reject(err);
  });
};

export default router;